@import url('https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  #root {
    @apply text-gray-400 text-lg font-semibold h-screen flex flex-col
  }
  #root .container {
    @apply flex-1
  }
  body{
    @apply font-Oxygen
  }
}

@layer components{
  .card{
    @apply bg-white text-gray-700
    w-72 h-72 min-h-[10rem] shadow-lg rounded-md overflow-hidden items-center justify-center
  }
  .obj{
    @apply bg-red-400
  }
}
